<template>
  <span class="badge" v-bind:data-badge="badge"><slot></slot></span>
</template>

<script>
/**
 * ZBadge
 * 
 * @author Maciej Lisowski <maciej.lisowski.elk@gmail.com>
 */
export default {
  name: 'ZBadge',
  props: {
    badge: {
      type: String
    },
  },
  computed: {
    trClass: function() {
      let css = ''

      if (this.active === true) {
        css += ' active'
      }

      return css
    }
  }
}
</script>
